.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.counter-sec{
  height: 100vh;
  background-color: rgb(0, 42, 255);
  justify-content: center;


}


.counter{
  background-color: rgb(157, 157, 200) ;
  min-width: 450px;
  padding: 2rem;
  justify-content: center;
  
  

}
.buttons{
  display: flex;
}
.lo{
  display: flex;
  background-color:  pink;
  border-radius: 15px;
  width: 10px;
  size: 18px;
  font-size: large;

}